@import url(https://fonts.googleapis.com/css?family=Lato:100,300,400,700,900);
@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.6.3/css/font-awesome.min.css);
@import url(https://fonts.googleapis.com/css?family=Lato:100,300,400,700,900);
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  background: #221f1f;
  color: #ffffff;
  font-family: "Lato", sans-serif;
}

h2{font-size:24px;font-weight:600;line-height:1.4;margin-bottom:1em}.TitleList{padding:20px 40px;box-sizing:border-box;transition:opacity 3s ease;opacity:0;position:relative;z-index:4}.TitleList .Title{font-size:24px;font-weight:600;line-height:1.4;margin-bottom:1em}.TitleList .titles-wrapper{display:flex;flex-wrap:wrap;margin:20px 0;width:calc(100vw - 80px);box-sizing:border-box;justify-content:space-between}p{font-size:15px;font-weight:300;line-height:1.6;margin-bottom:1em}.Header{background:linear-gradient(to bottom, black 0, transparent 100%);filter:progid:DXImageTransform.Microsoft.gradient(startColorstr="#a6000000", endColorstr="#00000000", GradientType=0);display:flex;padding:40px;position:fixed;top:0;left:0;width:100vw;box-sizing:border-box;z-index:5}.Logo{width:151px;height:41px;margin-right:10px;transition:margin .125s ease}.Logo svg{transform:scale(0.5);transform-origin:0 0}.Logo svg path{fill:#e50914;transition:fill .125s ease}.Logo:hover{cursor:pointer;margin-top:2px}.Logo:hover svg path{fill:#fff !important}.Search{align-items:center;width:100%;position:absolute;left:0;display:flex;justify-content:center;pointer-events:none}.Search input{font-family:Lato,sans-serif;font-weight:300;font-size:14px;height:37px;width:20%;background:rgba(0,0,0,0);color:#fff;-webkit-appearance:none;appearance:none;border:0;border:2px solid rgba(245,245,241,.1);outline:none;border-radius:37px;padding:0 10px;pointer-events:all;transition:border .25s ease,width .125s ease .125s}.Search input:focus{border:2px solid #e50914;width:30%}.UserProfile{margin-left:auto}.UserProfile .User{display:flex}.UserProfile .User .image{border-radius:44px;margin-left:10px;overflow:hidden;width:44px;height:44px;box-sizing:border-box;border:3px solid rgba(0,0,0,0);transition:border .125s ease}.UserProfile .User .image img{width:100%;display:block}.UserProfile .User .name{align-items:center;display:flex;font-size:18px;font-weight:300;height:44px}.UserProfile .User:hover{cursor:pointer}.UserProfile .User:hover .image{border:3px solid #e50914}.UserProfile .UserProfile-menu{display:none}.Hero{width:100%;position:relative;background-size:cover;min-height:800px;margin-bottom:-15vw}.Hero .content{position:relative;z-index:4;width:500px;left:10vw;top:10vw}.Hero .content .logo{max-width:400px;display:block;position:relative;left:-5px}.Hero .content p{width:100%}.Hero .content .button-wrapper{display:flex;width:400px;margin-top:30px}.Hero .content .button-wrapper .Button:first-child{margin-right:10px}.Hero .overlay{background:linear-gradient(to bottom, #221f1f 0, rgba(34, 31, 31, 0) 100%);filter:progid:DXImageTransform.Microsoft.gradient(startColorstr="#a6000000", endColorstr="#00000000", GradientType=0);height:100%;position:absolute;z-index:3;top:0;left:0;width:100%;transform:rotate(180deg)}.Button{background:rgba(0,0,0,0);display:flex;height:44px;align-items:center;justify-content:center;text-decoration:none;color:#fff;padding:20px;box-sizing:border-box;border:2px solid rgba(245,245,241,.2);border-radius:44px;font-size:14px;font-weight:600;transition:border .125s ease,background .125s ease}.Button:hover{border:2px solid #f5f5f1}.Button[data-primary=true]{border:2px solid #e50914}.Button[data-primary=true]:hover{background:#e50914}.TitleList[data-loaded=true]{opacity:1}.Item{width:calc(20% - 10px);min-width:calc(20% - 10px);background-color:#000;flex:1 0 auto;background-position:center;background-size:100%;height:200px;background-repeat:no-repeat;overflow:hidden;margin-right:10px;transition:background 1s ease}.Item:last-child{margin-right:0}.Item .overlay{background:linear-gradient(to bottom, rgba(0, 0, 0, 0.75) 0, transparent 100%);filter:progid:DXImageTransform.Microsoft.gradient(startColorstr="#a6000000", endColorstr="#00000000", GradientType=0);padding:20px;position:relative;height:100%;pointer-events:none;opacity:0;transition:opacity .125s ease}.Item .overlay .title{font-size:22px;font-weight:200;opacity:0;transition:opacity .25s ease}.Item .overlay .rating{font-size:14px;opacity:0;transition:opacity .25s ease .125s}.Item .overlay .plot{font-size:14px;margin-top:100px;font-weight:300;line-height:1.6;opacity:0;overflow:hidden;text-overflow:ellipsis;transition:margin .25s ease .125s,opacity .25s ease .25s}.Item:hover{background-size:150%}.Item:hover .overlay{opacity:1;pointer-events:all}.Item:hover .overlay .plot{margin-top:10px;opacity:1}.Item:hover .overlay .title{opacity:1}.Item:hover .overlay .rating{opacity:1}.Item:hover .overlay .ListToggle{opacity:1}.Item:nth-child(n+6){margin-top:20px}.ListToggle{border:2px solid hsla(0,0%,100%,.2);width:32px;height:32px;font-size:16px;border-radius:32px;overflow:hidden;position:absolute;right:20px;top:20px;opacity:0}.ListToggle:hover{border:2px solid #fff}.ListToggle:hover div{top:-32px}.ListToggle div{position:absolute;top:0;left:0;height:64px;width:32px;transition:.125s ease}.ListToggle div i{display:flex;align-items:center;justify-content:center;height:32px;width:32px}.ListToggle[data-toggled=true]{background:#e50914;border:2px solid #e50914}.ListToggle[data-toggled=true] div{top:-32px}.Navigation ul{display:flex;height:37px;align-items:center;padding:0 10px}.Navigation ul li{font-weight:400;padding:7px 10px;font-size:14px;transition:background .125s ease;border-radius:3px}.Navigation ul li:hover{background:#e50914;cursor:pointer}
